:root {
  /* Color Variables */
  --header-footer-color: #1b4965;
  --body-background-color: #f4f6f8;
}

* {
  font-family: "Nunito Sans", sans-serif;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #f4f6f8;
  background-color: var(--body-background-color);
  overflow-x: hidden;
}

.text-bold {
  font-weight: 700;
}

.text-bolder {
  font-weight: 900;
  font-style: italic;
}

.wrapper {
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  grid-template-rows: auto;
  grid-template-areas:
    "navbar-fill navbar navbar"
    "content content content"
    "footer footer footer";
}

.navbar-fill {
  background-color: #1b4965;
  background-color: var(--header-footer-color);
}

.navbar {
  color: white;
  grid-area: navbar;
  font-size: 28px;
  font-weight: bold;
  background-color: #1b4965;
  background-color: var(--header-footer-color);
  padding: 1rem 0;
}

.content {
  grid-area: content;
  display: grid;
}

.content.home-content {
  padding: 120px 0;
  grid-gap: 120px 0;
  gap: 120px 0;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: auto;
  grid-template-areas:
    ". hero ."
    "memo memo memo"
    "sample sample sample";
}

.hero {
  grid-area: hero;
  grid-gap: 1rem;
  gap: 1rem;
  display: grid;
  align-items: center;
  grid-template-columns: repeat(3, 250px);
}

.hero .showcase-container {
  display: flex;
  align-items: center;
  grid-column: span 3;
  margin-bottom: 110px;
}

.hero .showcase-svg {
  margin-right: 3rem;
}

.hero .showcase-text #showcase-header {
  font-size: 40px;
}

.hero .subject-btn,
.hero .subject-btn:is(:active, :focus) {
  height: 98px;
  font-size: 25px;
  position: relative;
  color: white;
  background-color: #1b4965;
  padding: 1rem 1rem 1rem 7rem;
  /* border-radius: 10px; */
  border: none;
  outline: none;
  text-decoration: none;
  /* Center Text on Link */
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.hero .subject-btn:hover {
  cursor: pointer;
}

.hero .subject-btn .button-svg {
  width: auto;
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 10px;
}

.memo {
  grid-area: memo;
  background-color: #e6ecf2;
  text-align: center;
  font-size: 45px;
  padding: 1rem 5rem;
}

.sample {
  grid-area: sample;
  position: relative;
  height: 300px;
}

.samples-container {
  position: absolute;
  display: flex;
  width: 100%;
  justify-content: space-evenly;
}

.rectangles-container {
  position: absolute;
  display: flex;
  justify-content: space-evenly;
  width: 110%;
  top: 110px;
  left: -80px;
}

.rectangle {
  width: 480px;
  height: 85px;
  -webkit-transform: rotate(24deg);
          transform: rotate(24deg);
  background: #ffd26d;
}

.rectangle-short {
  width: 450px;
}

.sample-temp {
  width: 300px;
  height: 350px;
  background: white;
  -webkit-filter: drop-shadow(2px 4px 6px black);
          filter: drop-shadow(2px 4px 6px black);
}

.content.worksheet-customization-content {
  grid-template-columns: max(300px, 20vw) max(600px, 60vw) max(300px, 20vw);
  grid-template-rows: 75vh auto;
  grid-column-gap: 10px;
  grid-column-gap: 10px;
  -webkit-column-gap: 10px;
          column-gap: 10px;
}

footer {
  grid-area: footer;
  background-color: #1b4965;
  background-color: var(--header-footer-color);
  padding: 2rem 6rem;
  color: white;
}

